.navbar {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	/* flex: 0 60px; */
	min-height: 50px;
	padding: 0 10px;
	background: #ffffff;
	border-bottom: 1px solid #d4dadf;
	-webkit-box-shadow: 0 1px 1px 0 #f0f1f4;
	        box-shadow: 0 1px 1px 0 #f0f1f4;
}

.navbar a {
	outline: none;
	color: #000;
	text-decoration: none;
	font-weight: bold;
}

.navbar__logo {
	color: var(--cintia-base-purple)
}

@media (min-width: 769px) {
	.navbar {
		padding: 0 10%;
	}
}


