.navbar-menu__botao {
	cursor: pointer;
	padding: 0.5rem;
	border: 1px solid var(--cintia-dark-purple);
	border-radius: 5px;
}

.navbar-menu__botao--aberto:after {
	content: "";
	position: fixed;
	top: 0;
	right: 0;
	height: 100vh;
	width: 100vw;
	background: rgba(0, 0, 0, 0.5);
}

.navbar-menu__opcoes {
	list-style: none;
	color: white;
	font-weight: bold;
	position: fixed;
	top: 0;
	left: 0;
	height: 30vh;
	width: 100vw;
	padding: 2rem 1rem 0;
	border-bottom-right-radius: 10px;
	margin: 0;
	background: var(--cintia-white-purple);
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
	-webkit-transition: -webkit-transform 1s ease-in-out;
	transition: -webkit-transform 1s ease-in-out;
	-o-transition: transform 1s ease-in-out;
	transition: transform 1s ease-in-out;
	transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

.navbar-menu__opcoes--aberto {
	-webkit-transform: translateX(0);
	    -ms-transform: translateX(0);
	        transform: translateX(0);
}

.navbar-menu__opcoes--ativo {
	border-bottom: 2px solid #000;
	outline: 0;
}

.navbar-menu__span {
	color: var(--cintia-dark-purple)	
}

.navbar-menu__opcoes li + li {
	margin-top: 1rem;
}

.navbar-menu__opcoes li a {
	cursor: pointer;
	color: #000;
	text-decoration: none;
}

@media (min-width: 992px) {
	.navbar-menu__botao {
		display: none;
	}

	.navbar-menu__opcoes {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
		position: initial;
		height: initial;
		width: initial;
		padding: initial;
		background: initial;
		-webkit-transform: initial;
		    -ms-transform: initial;
		        transform: initial;
		-webkit-transition: initial;
		-o-transition: initial;
		transition: initial;
	}

	.navbar-menu__opcoes--aberto {
		-webkit-transform: initial;
		    -ms-transform: initial;
		        transform: initial;
	}

	.navbar-menu__opcoes--ativo {
		border-color: var(--cintia-dark-purple);
	}

	.navbar-menu__opcoes li + li {
		margin-top: 0;
		margin-left: 1rem;
	}

	.navbar-menu__opcoes li a {
		/* color: var(--cintia-dark-purple); */
		padding: 0.3rem 0;
	}
}


